html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/ClickyChristmas/static/media/beautiful-beauty-berry-301544.797b9b4b.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
    max-width: 70%;
    border-radius: 10px;
}
.App {
  min-height: vh100;
}

.container {
  max-width: 100%;
}

.bg-green {
  background-color: rgb(0, 74, 23);
}

.text-green {
  color: rgb(0, 74, 23);
}

a {
  color: rgb(104, 244, 230);
}

a:hover {
  color: rgb(173, 1, 1)
}
